const isEqual = require('lodash/isEqual');
const cloneDeep = require('lodash/cloneDeep');

function compareItem(item1, item2) {
  function parseCompulsory(item) {
    let res = {};
    for (let option of item.compulsory_options)
      res[option.name] = option.selected;
    return res;
  }

  function parseOptional(item) {
    let res = {};
    for (let option of item.optional_options)
      res[option.name] = option.options.filter(obj => obj.selected).map(obj => obj.name);
    return res;
  }

  function checkMessage(item1, item2) {
    if (item1.message.selected !== item2.message.selected) return false;
    if (item1.message.selected === false) return true;  // means both false, message doesn't matter
    if (item1.message.text === item2.message.text) return true;  // both true, both same message
    return false;
  }

  let compareId = item1.id === item2.id;
  let compareCompulsory = isEqual(parseCompulsory(item1),
                                  parseCompulsory(item2));
  let compareOptional = isEqual(parseOptional(item1),
                                parseOptional(item2));
  let compareMessage = checkMessage(item1, item2);
  return compareId && compareCompulsory && compareOptional && compareMessage;
}

function unitCost(item) {
  if (item === null) return 0;

  let parsedItem = cloneDeep(item);
  for (let optionSet of item.compulsory_options) {
    let selection = optionSet.selected;
    optionSet.options = [optionSet.options[selection]];
  }
  for (let optionSet of item.optional_options) {
    optionSet.options = optionSet.options.filter(option => option.selected);
  }

  function sum(arr) {
    let add = (x, y) => (x + y);
    return arr.reduce(add, 0);
  }
  function sumOptions(options) {
    let total = 0;
    for (let optionSet of options) {
      let costs = optionSet.options.map(option => option.cost);
      total += sum(costs);
    }
    return total;
  }

  let unitCost = parseFloat(parsedItem.base_price)
                 + sumOptions(parsedItem.compulsory_options)
                 + sumOptions(parsedItem.optional_options);
  return unitCost;
}

const state = { cart: [] };

const getters = {
  totalItems(state) {
    let total = 0;
    for (let item of state.cart) total += item.quantity;
    return total;
  },
  totalItemsById: (state) => (id) => {
    let total = 0;
    for (let item of state.cart) {
      if (item.id === id) total += item.quantity;
    }
    return total;
  },
  totalPrice: (state) => {
    let total = 0;
    for (let item of state.cart)
      total += item.unitCost * item.quantity;
    return total;
  },
};

const mutations = {
  addItem(state, item) {
    for (let other of state.cart) {
      if (compareItem(item, other)) {
        other.quantity += item.quantity;
        return state.cart;
      }
    }
    item['unitCost'] = unitCost(item);
    state.cart.push(item);
  },
  removeItem(state, index) {
    state.cart.splice(index, 1);
  },
};

const actions = {};

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
};
