<template>
  <v-list-tile>
    <v-list-tile-content>
      <v-list-tile-title>
        ({{ item.quantity }}x) 
        {{ item.name }}
      </v-list-tile-title>
      <v-list-tile-sub-title>
        <ul class="text-capitalize">
          <li v-for="options in item.compulsory_options">
            {{ options.name }}: 
            {{ options.options[0].name }}
          </li>
          <li v-for="options in item.optional_options"
            v-if="options.options.length > 0"
          >
            {{ options.name }}: 
            {{ options.options.map(obj => obj.name).join(', ') }}
          </li>
        </ul>
      </v-list-tile-sub-title>
    </v-list-tile-content>

    <v-list-tile-avatar>
      {{ formatted.price }}
    </v-list-tile-avatar>

    <v-list-tile-avatar>
      <v-btn icon @click="$emit('remove')">
        <v-icon>delete</v-icon>
      </v-btn>
    </v-list-tile-avatar>
  </v-list-tile>
</template>

<script>
export default {
  props: ['item'],
  computed: {
    formatted() {
      let price = this.item.unitCost;
      let quantity = this.item.quantity;
      return {
        price: `$${parseFloat(price * quantity).toFixed(2)}`,
      };
    },
  },
}
</script>
