<template>
  <v-dialog v-model="dialog" content-class="food-dialog">
    <v-card id="card" class="px-3 pb-3">
      <v-card-title primary-title class="pb-0">
        <dialog-title />
        <dialog-quantity />
      </v-card-title>

      <v-card-text>
        <compulsory-options />
        <optional-options />
        <custom-message />
      </v-card-text>

      <v-card-actions>
        <v-btn block depressed large
          id="submit-btn"
          class="mx-2 primary"
          @click="closeDialog"
        >
            {{ cost }} Add to cart
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import components from '@/components/nearby/fooddialog';
let {
  DialogTitle,
  DialogQuantity,
  CompulsoryOptions,
  OptionalOptions,
  CustomMessage,
} = components;

export default {
  components: {
    'dialog-title': DialogTitle,
    'dialog-quantity': DialogQuantity,
    'compulsory-options': CompulsoryOptions,
    'optional-options': OptionalOptions,
    'custom-message': CustomMessage,
  },
  computed: {
    dialog: {
      get() { return this.$store.state.orders.dialog },
      set(value) {
        if (value) this.$store.commit('orders/openDialog');
        else this.$store.commit('orders/closeDialog');
      },
    },
    cost() {
      let cost = this.$store.getters['orders/currentItemCost'];
      return `$${ cost.toFixed(2) }`;
    },
  },
  methods: {
    closeDialog() {
      let item = this.$store.getters['orders/parseCurrentItem'];
      let valid = this.$store.getters['orders/validateCurrentItem'];

      if (valid) {
        this.$store.commit('cart/addItem', item);
        this.dialog = false;
      }
    },
  },
};
</script>

<style>
.food-dialog {
  position: absolute;
  bottom: 0px;
  margin: 0px;
  border-radius: 0px;
}
</style>

<style lang="scss" scoped>
@import '@/assets/css/main.scss';

#card {
  border-radius: 0px;
}

#submit-btn {
  font-size: 18px;
  border-radius: 8px;
}
</style>
